import merchant from "./merchant";
import { extractOpenHourData } from "@/util/openingHours";

export default {
  name: "openingHours",
  mixins: [merchant],
  computed: {
    openingHours() {
      const {
        restaurant_opening_time = "", 
        restaurant_open_date = "",
        open_dates = "",
        open_date = "", // TODO: ask maron to change this to open_dates
        open_days = "",
      } = this.merchant || {};

      return extractOpenHourData({
        restaurant_opening_time: restaurant_opening_time || open_days,
        restaurant_open_date: restaurant_open_date || open_dates || open_date,
      });
    },
  },
};
